body {
  color: #fafafc;
  background: #191923;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.float {
  position: absolute;
}

.left {
  left: 0;
}

.right {
  right: 0;
}

.top {
  top: 0;
}

.bottom {
  bottom: 0;
}

.button-nondecoration {
  padding: 0;
  border: none;
  background: none;
}

.w80 {
  width: 80%;
}

.coursecomp__wrapper_outdec {
  display: flex;
  justify-content: center;
}

.btn-success {
  border: none;
  background-color: #52c41a;
}

.btn-danger {
  border: none;
  background-color: #ff4d4f;
}

.btn-blank {
  border: none;
  background-color: white;
}

.btn-warning {
  border: none;
  background-color: #faad14;
}

/* table */
.table th {
  overflow-x: hidden;
}

.table td {
  padding: 0;
  overflow-x: hidden;
}

.table .non-input {
  padding: 8px;
}

.table thead th {
  padding: 0;
  vertical-align: middle;
  text-align: center;
}

.table {
  width: initial;
  margin-bottom: initial;
}

.color {
  color: #fafafc;
}

.bg-color {
  background-color: #fafafc;
}

.sc-color {
  color: #8c8c8e;
}

.o-color {
  color: #ffc107;
}

.bg-sc-color {
  background-color: #8c8c8e;
}

.divider {
  width: 100%;
  height: 1px;
  opacity: 0.3;
}

/* Tailwind */
@tailwind base;
@tailwind components;
@tailwind utilities;
