.calendar {
  /* width: 250px; */
  border: 1px solid #bfbdbe;
  border-radius: 4px;
}

.calendar .item {
  position: relative;
  /* width: calc(100% /7); */

  /* height: 30px;
  line-height: 30px; */
  display: inline-block;
  background-color: white;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  z-index: 1;
  text-align: center;
}

.calendar .week.partion {
  width: calc(100% / 8.5);
  margin-right: calc(100% / 8.5 * 0.35);
  cursor: pointer;
}

.calendar .days {
  margin: 5px 0 8px 0;
}

.calendar .day.partion.showweek {
  width: calc(100% / 8.5);
}

.calendar .day.partion {
  width: calc(100% / 7);
}

.calendar .day {
  cursor: pointer;
}

.calendar .day.disabled {
  cursor: not-allowed;
}

.calendar .dayname {
  cursor: context-menu;
  font-weight: 500;
}

.day.selected {
  background-color: #79cef2;
  color: white;
  font-weight: 500;
}

.notsame-month {
  color: #bfbdbe;
}

.day.today {
  background-color: #319fff;
  color: white;
  font-weight: 500;
}

.calendar .header {
  display: flex;
  background-color: #319fff;
  font-weight: 500;
  padding: 3px 0;
}

.calendar .header .current {
  cursor: context-menu;
  flex: 1 1;
  text-align: center;
  line-height: 25px;
}

.calendar .header .previous,
.next {
  cursor: default;
  cursor: pointer;
  width: 30px;
  padding: 0 10px;
  font-size: 16px;
}

.calendar .header .next {
  text-align: right;
}

